<template>
  <div class="service-handle-conlog">
    <div class="service-handle-conlog__title">
      【<span class="service-handle-conlog__title__name">{{ serviceName }}</span
      >】操作日志
    </div>
    <div class="service-handle-conlog__list">
      <el-table class="service-handle-conlog__list__table" :data="tableData">
        <el-table-column align="center" label="时间" min-width="180" fixed>
          <template slot-scope="scope">{{
            scope.row.created_at | formatTime
          }}</template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="operation"
          label="操作"
          min-width="60"
        />
        <el-table-column align="center" label="服务描述" min-width="180">
          <template slot-scope="scope">{{ scope.row.remark || '-' }}</template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="reason"
          label="原因"
          min-width="120"
        />
        <el-table-column align="center" label="操作人员" min-width="100">
          <template slot-scope="scope">{{
            scope.row.create_user.name || '-'
          }}</template>
        </el-table-column>
        <el-table-column align="center" prop="ip" label="IP" min-width="120" />
        <el-table-column
          align="center"
          prop="browser"
          label="登录方式"
          fixed="right"
        />
      </el-table>
      <div class="service-handle-conlog__list__pagination">
        <el-pagination
          :current-page="page.current_page"
          :page-sizes="pageSizes"
          :page-size="page.page_size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="page.total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { getServiceLog } from '@/api/service'
export default {
  data() {
    return {
      tableData: [],
      pageSizes: [10, 20, 50],
      page: {
        total: 0,
        page_size: 10,
        current_page: 1
      }
    }
  },
  computed: {
    serviceName() {
      return this.$route.query.serviceName
    }
  },
  created() {
    this.getServiceLogFun()
  },
  methods: {
    getServiceLogFun() {
      const _query = this.page || {}
      _query.id = this.$route.params.id
      getServiceLog(_query).then(res => {
        if (res && res.code === 20000) {
          const resData = res.data.list
          const pageInfo = res.data
          this.tableData = resData
          this.page = {
            total: pageInfo.count,
            page_size: pageInfo.page_size,
            current_page: pageInfo.current_page
          }
        }
      })
    },
    async handleCurrentChange(page) {
      this.page.current_page = page
      this.getServiceLogFun()
    },
    async handleSizeChange(size) {
      this.page.page_size = size
      this.getServiceLogFun()
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~@/styles/variables.scss';

.service-handle-conlog {
  &__title {
    font-size: 20px;
    color: #{$textDeepBlock};
    &__name {
      line-height: 20px;
      color: #{$activeColor};
      letter-spacing: 0;
    }
  }
  &__list {
    padding-top: 30px;
    &__pagination {
      padding: 60px 0 40px;
      text-align: center;
    }
  }
}
</style>
