import request from '@/utils/request'

export function getServices(params) {
  return request({
    url: '/fd_service_item/list',
    method: 'get',
    params
  })
}
export function changeServerStatus(id, data) {
  return request({
    url: '/fd_service_item' + `/${id}/change_server_status`,
    method: 'put',
    data
  })
}
export function updateServiceInfo(id, data) {
  return request({
    url: '/fd_service_item' + `/${id}`,
    method: 'put',
    data
  })
}

export function getServiceLog(params) {
  return request({
    url: '/fd_service_item' + `/${params.id}/logs`,
    method: 'get',
    params
  })
}
